.section-row{
    display: flex;
    .w100p{
        width: 100%;
    }
    .w75p{
        width: 75%;
    }
    .w50p{
        width: 50%;
    }
    .w25p{
        width: 25%;
    }
    p{
        padding: 0 8px;
        text-align: left;
    }
    .tr{
        text-align: right;
        justify-content: flex-end;
        .label{
            text-align: right;
        }
    }
    .with-pointer{
        padding-left: 26px;
    }
    .label{
        font-size: 12px;
        font-weight: 700;
        color: #65686b;
    }
}
.date-and-bible-reading{
    display: flex;
    align-items: center;
    height: 100%;
}
.date-and-bible-reading,
.subtitle-heading,
.heading-section{
    color: #fff;
    font-weight: 700;
}
.heading-section{
    margin-bottom: 5px;
    .congregation{
        line-height: 1.4;
    }
    .centered{
        justify-content: center;
    }
    .title{
        text-align: center;
        font-size: 18px;
        line-height: 1.5;
        letter-spacing: 0.25px;
    }
}
.with-pointer{
    position: relative;
    &:before{
        content: "";
        position: absolute;
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
        background-color: #58595d;
        width: 6px;
        height: 6px;
        border-radius: 6px;
    }
}
.section-col{
    border: 1px solid #ccc;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.time-col{
    width: 60px;
    display: flex;
    border: 1px solid #ccc;
    align-items: center;
    justify-content: center;
    p{
        text-align: center;
    }
}
.time-col-container{
    .section-col{
        width: calc(100% - 60px);
    }
}
.first-section{
    .time-col-container{

    }
    .subtitle-heading{
        border-left: 0;
        background-color: #58595d;
    }
    .with-pointer{
        &:before{
            background-color: #58595d;
        }
    }
}
.second-section{
    .subtitle-heading{
        border-left: 0;
        background-color: #be8801;
    }
    .with-pointer{
        &:before{
            background-color: #be8801;
        }
    }
}
.third-section{
    .subtitle-heading{
        border-left: 0;
        background-color: #7d0024;
    }

    .with-pointer{
        &:before{
            background-color: #7d0024;
        }
    }
}
.section-row .section-col .section-row {
    .section-col{
        // border: 0;
        border-top: 0;
        border-bottom: 0;
    }
}
.MidWeekScheduleContainer{
    margin-bottom: 20px;
    border: 2px solid #ccc;
}